"use strict";

require("../docs.css");

require("@ebay/skin/expand-button");

require("@ebay/skin/menu-button");

var MenuButton = require('../../packages/makeup-menu-button');

window.onload = function () {
  document.querySelectorAll('.menu-button').forEach(function (el, i) {
    var widget = new MenuButton(el);
    widget.menu.el.addEventListener('makeup-menu-select', function (e) {
      return console.log(e.type, e.detail);
    });
    widget.menu.el.addEventListener('makeup-menu-change', function (e) {
      return console.log(e.type, e.detail);
    });
    widget.menu.el.addEventListener('makeup-menu-button-mutation', function (e) {
      return console.log(e.type, e.detail);
    });
  });
};